<template>
    <div class="row">
        <div class="col s12">
            <transition-group name="articles" enter-active-class="animated fadeInUp" class="collection z-depth-1 posts" tag="div">
                <div class="collection-item post-item" v-for="(post, index) in posts" :key="index">
                    <h5 class="title">
                        <router-link :to="'/posts/' + post.slug">{{ post.title }}</router-link>
                    </h5>
                    <div class="label article-info">
                        published at {{ post.created_at.substr(0, 10) }}
                        <i class="material-icons">visibility</i>{{ post.view_count }}
                        <i class="material-icons">comment</i>{{ post.comment_count }}
                        <a href="javascript:void(0)" class="favorite-btn">
                            <i class="material-icons">favorite_border</i>
                            <span>{{ post.favorite_count }}</span>
                        </a>
                    </div>
                    <p class="summary">{{ post.summary }}</p>
                    <div class="row no-margin">
                        <div class="col s12 m12 l12 no-padding">
                            <div class="chip" v-for="tag in post.tags">
                                <router-link :to="'/tags/' + tag.id">{{ tag.name }}</router-link>
                            </div>
                        </div>
                    </div>
                </div>
                <div class="collection-item" v-if="!hasMore" :key="0">
                    <p>whoops！No more posts here.</p>
                </div>
            </transition-group>
            <!-- No more posts -->
            <!-- loader -->
            <div class="loader-wrapper center">
                <circular-loader v-show="loading"></circular-loader>
            </div>
        </div>
    </div>
</template>
<script>
    import CircularLoader from './circular-loader.vue';
    export default {

        components: {
            CircularLoader
        },

    	data() {
    		return {
                page: 0,
    			posts: [],
                hasMore: true,
                loading: false,
    		}
    	},

    	mounted() {
            this.loadMorePosts();
    	},

        methods: {
            loadMorePosts() {
                if(this.hasMore && !this.loading) {
                    this.loading = true;
                    this.$http.get(`api/posts?page=${this.page + 1}`).then((response) => {
                        let body = response.body;
                        let posts = body.data;
                        for (let index in posts) {
                            this.posts.push(post[index]);
                        }
                        // this.posts = this.posts.concat(body.data);
                        this.page = body.current_page;
                        this.hasMore = body.next_page_url != null;
                        Materialize.scrollFire([{
                            selector: 'footer', 
                            offset: -100,
                            callback: this.loadMorePosts
                        }]);
                        this.loading = false;                      
                    }, (response) => {
                        // TODO
                        this.loading = false;                        
                    });
                }
            }
        }
    }
</script>